<template>
	<view class="flex align-center flex-column" style="">
		<view class="mt-1 mb-1" style="font-size: 68rpx;">
			<text>
				失踪人信息填报
			</text>
		</view>
		<view class="pd_2">
			<text style="font-size: 14px;margin-left: 12rpx;">
				失踪人照片*
			</text>
			<view class="avatar-uploader" style="margin: 40rpx auto;">
				<view v-if="!form.userpic" class="position_absolute" style="top: 90px;left: 70px;">
					<text style="font-size: 98rpx;color: #E3E3E3;">+</text>
				</view>
				<image style="width: 178px;" :src="form.userpic" mode="aspectFill" @tap="chooseImage" lazy-load="" ></image>
			</view>
			<el-form ref="form" :model="form" label-width="80px">
			  <el-form-item label="姓名信息*">
			    <el-input v-model="form.name"></el-input>
			  </el-form-item>
			  <el-form-item label="年龄信息*">
			    <el-input v-model="form.age"></el-input>
			  </el-form-item>
			  <el-form-item label="性别信息*">
			    <el-select v-model="form.gender" placeholder="请选择失踪人性别">
			      <el-option label="男" value="男"></el-option>
			      <el-option label="女" value="女"></el-option>
			    </el-select>
			  </el-form-item>
			 <!-- <el-form-item label="失踪时间">
			    <el-col :span="11">
			      <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
			    </el-col>
			    <el-col class="line" :span="2">-</el-col>
			    <el-col :span="11">
			      <el-time-picker placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
			    </el-col>
			  </el-form-item> -->
			  <el-form-item label="是否报警">
			    <el-switch v-model="form.police"></el-switch>
			  </el-form-item>
			  <el-form-item label="样貌特征*">
			    <el-checkbox-group v-model="form.features">
			      <el-checkbox label="高" name="type"></el-checkbox>
			      <el-checkbox label="胖" name="type"></el-checkbox>
			      <el-checkbox label="矮" name="type"></el-checkbox>
			      <el-checkbox label="黑" name="type"></el-checkbox>
			    </el-checkbox-group>
			  </el-form-item>
			  <el-form-item label="特殊情况">
			    <el-checkbox-group v-model="form.special">
			      <el-checkbox label="身体障碍" name="type"></el-checkbox>
			      <el-checkbox label="精神不正常" name="type"></el-checkbox>
			    </el-checkbox-group>
			  </el-form-item>
			  <el-form-item label="失踪位置">
			    <view @click="getLocation">
					<text style="color: rgba(0,0,0,0.8)" v-if="form.gps.latitude == '' && form.gps.longitude == ''">{{form.location}}</text>
					<text style="color: rgba(0,0,0,0.8)" v-else>经度:{{form.gps.longitude}} 纬度:{{form.gps.latitude}}</text>
				</view>
			  </el-form-item>
			  <el-form-item label="失踪原因">
			    <el-input type="textarea" v-model="form.reason"></el-input>
			  </el-form-item>
			  <el-form-item label="其它备注">
			    <el-input type="textarea" v-model="form.desc"></el-input>
			  </el-form-item>
			  <view class="flex justify-center" style="margin-right: 80px;">
				  <el-form-item>
				    <el-button type="primary" @click="onSubmit">发布找寻任务</el-button>
				  </el-form-item>
			  </view>
			</el-form>
		</view>
	</view>
</template>

<script>
	var sourceType = [
		['camera'],
		['album'],
		['camera', 'album']
	]
	var sizeType = [
		['compressed'],
		['original'],
		['compressed', 'original']
	]
	export default {
		data() {
			return {
				form: {
				  picid:'',
				  userpic:'',
				  name: '',
				  age: '',
				  gender: '',
				  date1: '',
				  date2: '',
				  place:'',
				  police: false,
				  features: [],
				  special: [],
				  reason:'',
				  desc: '',
				  location:'点击获取当前定位',
				  gps:{  // 北京默认地址
					  latitude:'116.478984',
					  longitude:'39.910138'
				  }
				},
				sourceTypeIndex: 2,
				sourceType: ['拍照', '相册', '拍照或相册'],
				sizeTypeIndex: 2,
				sizeType: ['压缩', '原图', '压缩或原图'],
				countIndex: 8,
				count: [1, 2, 3, 4, 5, 6, 7, 8, 9],
				codeTime:0,
			}
		},
		onLoad() {
			uni.request({
				url:'/dpc/cityjson?ie=utf-8',
				method:'GET',
				success:(res)=> {
					let data = JSON.parse(res.data.slice(19,res.data.length-1))
					this.form.place =  data.cname
				}
			})
		},
		methods: {
			chooseImage: async function() {
				// #ifdef APP-PLUS
				// TODO 选择相机或相册时 需要弹出actionsheet，目前无法获得是相机还是相册，在失败回调中处理
				if (this.sourceTypeIndex !== 2) {
					let status = await this.checkPermission();
					if (status !== 1) {
						return;
					}
				}
				// #endif
				uni.chooseImage({
					sourceType: sourceType[this.sourceTypeIndex],
					sizeType: sizeType[this.sizeTypeIndex],
					success: (res) => {
						this.UploadImages(res.tempFilePaths[0])
					},
					fail: (err) => {
						// #ifdef APP-PLUS
						if (err['code'] && err.code !== 0 && this.sourceTypeIndex === 2) {
							this.checkPermission(err.code);
						}
						// #endif
						// #ifdef MP
						uni.getSetting({
							success: (res) => {
								let authStatus = false;
								switch (this.sourceTypeIndex) {
									case 0:
										authStatus = res.authSetting['scope.camera'];
										break;
									case 1:
										authStatus = res.authSetting['scope.album'];
										break;
									case 2:
										authStatus = res.authSetting['scope.album'] && res.authSetting['scope.camera'];
										break;
									default:
										break;
								}
								if (!authStatus) {
									uni.showModal({
										title: '授权失败',
										content: 'Hello uni-app需要从您的相机或相册获取图片，请在设置界面打开相关权限',
										success: (res) => {
											if (res.confirm) {
												uni.openSetting()
											}
										}
									})
								}
							}
						})
						// #endif
					}
				})
			},
			async UploadImages(filePaths){
				uni.uploadFile({
					url: 'http://124.70.75.88:8888/urlimage/',
					filePath:filePaths,
					name:'TaskImage',
					success: (res) => {
						let data = JSON.parse(res.data);
						this.form.userpic = data.url
						this.form.picid = data.id
						this.$message.success('上传图片成功');
					},
					fail:(res)=> {
						this.$message.error('上传图片失败');
					}
				})
			},
			onSubmit(){
				new Promise(tt=>{
					uni.request({
						url:'http://124.70.75.88:8888/task/',
						method:'POST',
						data:{
							TaskName:this.form.name + ' 搜救小队',
							RescuedName:this.form.name,
							RescuedAge:this.form.age,
							RescuedSex:this.form.gender,
							RescuedFeatures:this.form.features,
							RescuedPlace:this.form.place,
							// RescuedTime:'',
							
							
							RescueLevel: 'S',
							
							url:this.form.userpic,
							RescuedReason:this.form.reason,
							TaskCallPolice:this.form.police,
							TaskExc:this.form.special,
							TaskOtherRemarks:this.form.desc,
							Rescuedlongitude:this.form.gps.longitude,
							Rescuedlatitude:this.form.gps.lat
						},
						success: (res) => {
							tt(res.data)
						}
					})
				}).then((res)=>{
					uni.request({
						url: 'http://124.70.75.88:8888/group/',
						method: 'POST',
						data: {
							TID: res.TID,
							GName: res.TaskName,
							GUrl: res.url
						},
						success: (ret) => {
							console.log(ret.data)
						}
					})
				})
			},
			getLocation(){
				uni.getLocation({
				    type: 'gcj02',
					geocode	:'true',
				    success:(res) => {
						this.form.gps.longitude = res.longitude
						this.form.gps.latitude = res.latitude
				    },
					fail: (err) => {
						console.log(err)
					}
				});
			},
			createTask() {
				
				
			}
		}
	}
</script>

<style>
	page{
		width: 100%;
		height: 100%;
		/* background-image: url(../../static/coolHue-90F7EC-32CCBC.png); */
		background-size: 100% 100%;
		background-repeat: no-repeat;
	}
	.avatar-uploader {
		width: 178px;
	    border: 1px solid #DCDFE6;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	  }
	  .avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	  }

</style>
